import React from "react";

const Filter = props => {
  const { blogs, handleDelete } = props;

  const allBlogs = blogs.map(data => (
    <li key={data.id}>
      {data.content} {data.name} {data.url}
      <button className="deletor" onClick={() => handleDelete(data.id)}>
        Delete
      </button>
    </li>
  ));
  return <div>{allBlogs}</div>;
};

export default Filter;
