import React, { useState, useEffect } from "react";
import Filter from "./Filter";
import Notification from "./Notification";
import axios from "axios";
import blogService from "./services/blogs";
import "./index.css";

const App = () => {
  const [blogs, setBlogs] = useState([]);
  const [newTitle, setNewTitle] = useState([""]);
  const [newAuthor, setNewAuthor] = useState([""]);
  const [newUrl, setNewUrl] = useState([""]);
  const [errorMessage, setErrorMessage] = useState("");

  const resetFields = () => {
    setNewTitle("");
    setNewAuthor("");
    setNewUrl("");
  };

  useEffect(() => {
    blogService.getAll().then((allBlogs) => {
      setBlogs(allBlogs);
    });
  }, []);

  const addBlog = (event) => {
    event.preventDefault();
    const newObject = {
      content: newTitle,
      name: newAuthor,
      url: newUrl,
      id: Math.random() * 1000,
    };

    const addedName = newTitle;

    const blogList = blogs.filter((b) => addedName === b.content);

    if (blogList.length !== 0) {
      const id = blogList[0].id;
      if (window.confirm("Blog is already in the list, replace it?")) {
        blogService.update(id, { ...newObject, id: id }).then((newBlog) => {
          console.log(newBlog);

          setBlogs(blogs.map((blog) => (blog.id !== id ? blog : newBlog)));
        });
      }
      resetFields();
      return;
    }

    axios.post("http://localhost:3001/blogs", newObject).then((response) => {
      setBlogs(blogs.concat(response.data));
      setNewTitle("");
      setNewAuthor("");
      setNewUrl("");
      setErrorMessage(`New blog called ${newObject.content} was added`);
      setTimeout(() => {
        setErrorMessage(null);
      }, 5000);
    });
    resetFields();
    return setBlogs(blogs.concat(newObject));
  };

  const handleTitleChange = (title) => {
    setNewTitle(title.target.value);
  };

  const handleAuthorChange = (author) => {
    setNewAuthor(author.target.value);
  };

  const handleUrlChange = (u) => {
    setNewUrl(u.target.value);
  };

  const handleDelete = (id) => {
    const kill = blogs.filter((blog) => blog.id !== id);
    if (window.confirm("Do you really want to delete this blog?")) {
      blogService.destroy(id);
      setBlogs(kill);
      setErrorMessage(`Blog was removed`);
      setTimeout(() => {
        setErrorMessage(null);
      }, 5000);
    }
  };

  return (
    <div>
      <h1>Blog List</h1>
      <form onSubmit={addBlog}>
        <Notification message={errorMessage} />
        <h4>Add New</h4>
        <div>
          Title:
          <input value={newTitle} onChange={handleTitleChange} />
        </div>
        <div>
          Author:
          <input value={newAuthor} onChange={handleAuthorChange} />
        </div>
        <div>
          Url:
          <input value={newUrl} onChange={handleUrlChange} />
        </div>
        <div>
          <button type="submit">Add</button>
        </div>
      </form>
      <h4>Added Blogs:</h4>
      <Filter blogs={blogs} handleDelete={handleDelete} />
    </div>
  );
};
export default App;
